.balance-card {
  min-width: 250px;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0, 83, 250, 0.34);
  text-align: center;
  padding: 14px 0;
  margin-bottom: 40px;
}
.balance-card > * {
  padding: 0 14px;
}
.balance-card .footer {
  display: flex;
  align-items: center;
  border-top: 1px solid #B5B9C5;
  padding: 14px 30px 0 14px;
  line-height: 16px;
}
.balance-card .footer > span {
  border-bottom: 1px solid #0053FA;
}
.balance-card .footer .dot3-wrapper {
  color: #0053FA;
  padding: 5px;
  position: absolute;
  right: 5px;
}
.balance-card .footer .dot3-wrapper svg {
  transform: rotate(90deg);
  transform-origin: top center;
}
